#product-inner img {
  width: 300px;
  height: 300px;
  object-fit: cover;
}

.slide-wr {
  text-align: center;
}

.slide-wr img {
  display: block;
  width: 300px;
  height: 300px;
  object-fit: cover;
  margin: 0 auto;
}

h2 {
  font-family: 'Sebang Gothic';
}

@media all and (max-width:1280px) {
  .slide-wr img {
    width: calc(23.4375vw - 3rem);
    height: calc(23.4375vw - 3rem);
  }
}