@font-face {
  font-family: "Sebang Gothic";
  font-weight: normal;
  src: url('./fonts/SEBANG\ Gothic\ OTF.otf');
}

@font-face {
  font-family: "Sebang Gothic";
  font-weight: bold;
  src: url('./fonts/SEBANG\ Gothic\ OTF\ Bold.otf');
}

@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100..900&display=swap');

* {
  margin: 0; padding: 0;
  box-sizing: border-box;

  font-size: 16px;
  font-family: 'Noto Sans KR';
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

/* 시작 */

.App {
  padding-top: 60px;
}

#main-img {
  width: 100%;
  height: 600px;
}

#main-img > img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

@media all and (max-width:1280px) {
  #main-img {
    height: 46.875vw;
    min-height: 300px;
  }
}