.detail-product-info {
  width: 700px;
}

.detail-product-img {
  width: 500px;
  height: 500px;
}

.detail-product-img > img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.detail-product-btn-wr > button {
  width: 49%;
  border: 1px solid #222;
  font-size: 1.5rem;
  font-weight: bold;
  background-color: #fff;
  color: #222;
  padding: 0.5rem 0;
}

#buying-btn {
  background-color: #222;
  color: #fff;
}

.info-wr > li:not(:last-child) {
  border-right: 1px solid;
}